import React, { useEffect, useMemo } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { doPrint } from 'src/store/slice'

const Printer: React.FC = () => {
    const dispatch = useDispatch()
    const print: boolean = useSelector((state: any): boolean => state.app.print)
    // mount
    useEffect(() => {
        if( print ) { setTimeout(() => (window as any).print(), 100) }
    }, [print]);

    (window as any).onafterprint = () => {
        dispatch(doPrint(false))
    };

    return useMemo(() => (
        <></>
    ), []);
};

export default Printer;
