import React from "react";
import { useDeviceDetect } from "src/hooks/useDeviceDetect";
import Left from "src/components/Left";
import Project from "src/components/Project";
import Skill from "src/components/Skill";
import { useSelector, useDispatch } from 'react-redux'
import { setPage } from "src/store/slice";

const PageTwo: React.FC = () => {
    const dispatch = useDispatch();
    const print = useSelector((state: any) => state.app.print)
    const device = useDeviceDetect();

    return (
        <article
            className="active"
            onClick={() => device.isMobile() && dispatch(setPage(1))}
        >
            <div className="row">
                {!print ? (
                    <div className="col-3 p-0">
                        <Left klassName="col-md-3-3" />
                    </div>
                ) : (
                    ""
                )}
                <div className={`right ${print ? "col-12 ml-4" : "col-9"}`}>
                    <Project />
                    <Skill />
                </div>
            </div>
        </article>
    );
};

export default PageTwo;
