import React, { useMemo } from "react";

interface PropProperties {
    projects: any[];
}

const ProjectTimeline: React.FC<PropProperties> = (props: PropProperties) => {
    const { projects } = props;

    return useMemo(() => (
        <>
            {projects && projects.length ?
                <ul className="work-project-list">
                    {projects
                        .map((wp: any, i: any) => (
                            <li key={`project----${i}`}>
                                <h4>{wp.name}</h4>
                                {wp.description && wp.description.length ? (
                                    <ul className="project-desc">
                                        {wp.description.map((desc: string, i: number) => (
                                            <li key={`wp--description--${i}`}>
                                                <i className="fa fa-certificate" />&nbsp;<span>{desc}</span>
                                            </li>
                                        ))}
                                    </ul>
                                ) : (
                                    ""
                                )}
                            </li>
                        ))}
                </ul>
                :
                <></>
            }
        </>
    ), [projects]);
};

export default ProjectTimeline;
