import React, { useMemo } from "react";
import Avatar from "src/components/Avatar";
import Translator from "src/components/Translator";
import { useSelector } from 'react-redux'

interface PropProperties {
    klassName?: string;
}

const Left: React.FC<PropProperties> = (props: PropProperties) => {
    const { klassName } = props;
    const cv: any = useSelector((state: any) => state.app.cv)

    return useMemo(() => (
        <div className="left">
            <div className={`info header ${klassName}`}>
                <Avatar src={cv?.contact?.avatar} />
                <div className="user-name">{cv?.contact?.name}</div>
                <div className="slogan">{cv.slogan}</div>
            </div>
            <div className="info">
                <div className="tag">
                    <Translator text="personal_info" />
                </div>
                <address className="contact">
                    <ul>
                        {cv.contact &&
                            cv.contact.address &&
                            cv.contact.address.map((info: any, i: number) => (
                                <li key={`contact--address--${i}`}>
                                    <i className={info.icon} />
                                    &nbsp;
                                    {info.link ? (
                                        <a
                                            href={info.link}
                                            target="_blank"
                                            rel="noreferrer"
                                        >
                                            {info.desc}
                                        </a>
                                    ) : (
                                        <span>{info.desc}</span>
                                    )}
                                </li>
                            ))}
                    </ul>
                    <ul>
                        {cv.socials &&
                            cv.socials.map((social: any, i: number) => (
                                <li key={`social---${i}`}>
                                    <i className={social.icon} />
                                    &nbsp;
                                    {social.link ? (
                                        <a href={social.link}>{social.desc}</a>
                                    ) : (
                                        <span>{social.desc}</span>
                                    )}
                                </li>
                            ))}
                    </ul>
                </address>
            </div>

            <div className="info">
                <div className="tag">
                    <Translator text="job" />
                </div>
                <ul className="job-intent">
                    {cv.job &&
                        Object.values(cv.job).map((entity: any, i: number) => (
                            <li key={`job--${i}`}>
                                {entity.name}: {entity.desc}
                            </li>
                        ))}
                </ul>
            </div>
            <div className="info">
                <div className="tag">
                    <Translator text="skills" />
                </div>
                <ul className="skills">
                    {cv.skills &&
                        Object.keys(cv.skills).map((value: any, i: number) => (
                            <li key={`skills--${i}`}>
                                <span className="skill-name">{value}</span>
                                <div className="skill-bar-wrap">
                                    <div
                                        className="skill-bar"
                                        style={{
                                            width: `${cv.skills[value]}%`,
                                        }}
                                    />
                                </div>
                            </li>
                        ))}
                </ul>
            </div>
            <div className="info">
                <div className="tag">
                    <Translator text="languages" />
                </div>
                <ul className="skills">
                    {cv.languages &&
                        Object.keys(cv.languages).map(
                            (value: any, i: number) => (
                                <li key={`languages--${i}`}>
                                    <span className="skill-name">{value}</span>
                                    <div className="skill-bar-wrap">
                                        <div
                                            className="skill-bar"
                                            style={{
                                                width: `${cv.languages[value]}%`,
                                            }}
                                        />
                                    </div>
                                </li>
                            )
                        )}
                </ul>
            </div>

            <div className="info bottom">
                <div className="tag">
                    <Translator text="evaluation" />
                </div>
                <p className="self-evaluation">{cv.evaluation}</p>
            </div>
        </div>
    ), [cv, klassName]);
};

export default Left;
