import React, { useMemo } from "react";
import { useSelector } from "react-redux";
import Translator from "src/components/Translator";
import DescriptionTimeline from "./DescriptionTimeline";
import ProjectTimeline from "./ProjectTimeline";

interface PropProperties {
    item: any;
}

const ItemTimeline: React.FC<PropProperties> = (props: PropProperties) => {
    const { item } = props;
    const print: boolean = useSelector((state: any) => state.app.print)
    const page: number = useSelector((state: any) => state.app.page)

    return useMemo(() => (
        <div className="timeline-item">
            <div className="timeline-item-time">{item.time}</div>
            <div className="timeline-item-project">
                <div className="circle" />
                <div className="project-info">
                    <span className="project-name">{item.company || item.name}</span>
                    <span className="work-title">{item.title}</span>
                    <span className="project-time">{item.time}</span>
                    <span className="project-link">{item.source}</span>
                    <a className="project-code" href={item.source} target="_blank" rel="noreferrer">
                        <i className="fa fa-link" />&nbsp;{print ? (item.source) : (<Translator text="source" />)}
                    </a>
                    <a className="project-code" href={item.online} target="_blank" rel="noreferrer">
                        <i className="fa fa-link" />&nbsp;{print ? (item.online) : (<Translator text="online" />)}
                    </a>
                </div>
                {page === 1 ? (
                    <ProjectTimeline projects={item.projects} />
                ) : (
                    <DescriptionTimeline descriptions={item.description} />
                )}
            </div>
        </div>
    ), [item, page, print]);
};

export default ItemTimeline;
