import React, { useMemo } from "react";

interface PropProperties {
    src: string;
}

const Avatar: React.FC<PropProperties> = (props: PropProperties) => {
    const { src } = props;

    return useMemo(() => (
        <figure className="avatar">
            <img className="img-fluid" src={src} alt="user avatar" />
        </figure>
    ), [src]);
};

export default Avatar;
