import React, { useEffect, useMemo, useState } from "react";
import Translator from "src/components/Translator";
import Timeline from "src/components/Timeline";
import { useSelector } from "react-redux";

const Work: React.FC = () => {
    const cv = useSelector((state: any) => state.app.cv)
    
    const [works, setWorks] = useState<any[]>([]);

    useEffect(() => {
        setWorks(cv.works || null);
    }, [cv]);

    return useMemo(() => (
        <>
            {works && works.length ? (
                <section className="works">
                    <div className="title-wrap d-flex align-items-center">
                        <i className="fa fa-briefcase" />
                        <span className="pl-5">
                            <Translator text="works" />
                        </span>
                    </div>
                    <div className="content">
                        <Timeline items={works} />
                    </div>
                </section>
            ) : (
                ""
            )}
        </>
    ), [works]);
};

export default Work;
