import React, { useMemo } from "react";
import ItemTimeline from "src/components/ItemTimeline";
interface PropProperties {
    items: any[];
}

const Timeline: React.FC<PropProperties> = (props: PropProperties) => {
    const { items } = props;

    return useMemo(() => (
        <div className="timeline">
            {items && items.length &&
                items.map((item: any, i: number) => (
                    <ItemTimeline item={item} key={`timeline--wrapper--${i}`} />
                ))}
        </div>
    ), [items]);
};

export default Timeline;
