import React, { useMemo } from "react";
import vi from "src/static/vi.svg";
import en from "src/static/en.svg";
import { useSelector, useDispatch } from 'react-redux'
import { doPrint, setLanguage, setPage } from 'src/store/slice'

const Toolbar: React.FC = () => {

    const dispatch = useDispatch()
    const print = useSelector((state: any) => state.app.print)
    const language = useSelector((state: any) => state.app.language)
    const page = useSelector((state: any) => state.app.page)

    return useMemo(() => (
        <>
            {!print ? (
                <aside className="vc--toolbar p-0 m-0">
                    <ul>
                        <li>
                            <span
                                className="cursor p-3 d-block"
                                onClick={() => dispatch(doPrint(!print))}
                            >
                                <i className="fa fa-print" />
                                &nbsp;
                            </span>
                        </li>
                        <li
                            onClick={() =>
                                dispatch(setLanguage(language === "vi" ? "en" : "vi"))
                            }
                        >
                            <span className="cursor p-3 d-block">
                                <img
                                    className="img-thumbnail vc--resume-flag"
                                    src={language === "vi" ? en : vi}
                                    alt=""
                                />
                            </span>
                        </li>
                        <li>
                            {page === 2 ? (
                                <span
                                    className="cursor p-3 d-block"
                                    onClick={() => dispatch(setPage(1))}
                                >
                                    <i className="fas fa-backward" />
                                </span>
                            ) : (
                                <span
                                    className="cursor p-3 d-block"
                                    onClick={() => dispatch(setPage(2))}
                                >
                                    <i className="fas fa-forward" />
                                </span>
                            )}
                        </li>
                    </ul>
                </aside>
            ) : (
                ""
            )}
        </>
    ), [print, page, language, dispatch]);
};

export default Toolbar;
