import React, { useMemo } from "react";
import Avatar from "src/components/Avatar";
import { useSelector } from 'react-redux'

const Header: React.FC = () => {
    const cv = useSelector((state: any) => state.app.cv)
    const print = useSelector((state: any) => state.app.print)

    return useMemo(() => (
        <>
            {print ? (
                <header className="vc--resume-header col-12">
                    <div className="row">
                        {cv.contact ? (
                            <>
                                <div className="contact-info col-4">
                                    {cv.contact.avatar ? (
                                        <Avatar src={cv.contact.avatar} />
                                    ) : (
                                        ""
                                    )}
                                </div>
                                <section className="title col-8">
                                    {cv.contact.name ? (
                                        <div className="user-name">
                                            {cv.contact.name}
                                        </div>
                                    ) : (
                                        ""
                                    )}
                                    {cv.job.position.desc ? (
                                        <div className="job-title">
                                            {cv.job.position.desc}
                                        </div>
                                    ) : (
                                        ""
                                    )}
                                </section>
                            </>
                        ) : (
                            ""
                        )}
                    </div>
                </header>
            ) : (
                ""
            )}
        </>
    ), [print, cv]);
};

export default Header;
