import React, { useEffect, useMemo, useState } from "react";
import { useSelector } from 'react-redux'


const ContactAdress: React.FC = () => {
    const cv = useSelector((state: any) => state.app.cv)
    const print = useSelector((state: any) => state.app.print)
    const [address, setAddress] = useState<any[]>([]);

    useEffect(() => {
        setAddress(cv?.contact?.address || []);
    }, [cv]);

    return useMemo(() => (
        <>
            {print && address ? (
                <div>
                    {address.map((info: any, i: number) => (
                        <div
                            className="information"
                            key={`contact-address--${i}`}
                        >
                            <span>
                                <i className={info.icon} />
                                &nbsp;
                                {info.link ? (
                                    <a
                                        href={info.link}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        {info.desc}
                                    </a>
                                ) : (
                                    <span className="pl-5">{info.desc}</span>
                                )}
                            </span>
                        </div>
                    ))}
                </div>
            ) : (
                ""
            )}
        </>
    ), [address, print]);
};

export default ContactAdress;
