import React, { useMemo } from "react";
import qrCode from "src/static/qr.png";
import Translator from "src/components/Translator";
import { useSelector, useDispatch } from 'react-redux'
import { setPage } from 'src/store/slice'

const Footer: React.FC = () => {
    const dispatch = useDispatch()
    const page = useSelector((state: any) => state.app.page)
    const print = useSelector((state: any) => state.app.print)

    return useMemo(() => (
        <>
            {!print ? (
                <footer className="vc--resume-main-footer col-12">
                    <div className="row">
                        <span
                            className={`col-4 text-left text-start ${page === 1 ? "invisible" : ""
                                }`}
                            onClick={() => dispatch(setPage(1))}
                        >
                            <Translator text="prev" />
                        </span>
                        <span className="col-4 text-center invisible">
                            &copy;
                            {new Date().getFullYear()} Me
                            <img
                                style={{ width: "40px" }}
                                className="img-reponsive img-thumbnail"
                                src={qrCode}
                                alt=""
                            />
                        </span>
                        <span
                            className={`col-4 text-right text-end ${page === 2 ? "invisible" : ""
                                }`}
                            onClick={() => dispatch(setPage(2))}
                        >
                            <Translator text="next" />
                        </span>
                    </div>
                </footer>
            ) : (
                ""
            )}
        </>
    ), [page, print, dispatch]);
};

export default Footer;
