import React from "react";
import { useSelector, useDispatch } from 'react-redux'
import { setPage } from 'src/store/slice'

import { useDeviceDetect } from "src/hooks/useDeviceDetect";
import Left from "src/components/Left";
import Translator from "src/components/Translator";
import ContactAdress from "src/components/ContactAdress";
import Social from "src/components/Social";
import Education from "src/components/Education";
import Work from "src/components/Work";


const PageOne: React.FC = () => {
    const dispatch = useDispatch()
    const print: any = useSelector((state: any) => state.app.print)

    const device = useDeviceDetect();

    return (
        <article
            className="active"
            onClick={() => device.isMobile() && dispatch(setPage(2))}
        >
            <div className="row">
                {!print ? (
                    <div className="col-3 p-0">
                        <Left klassName="col-md-3-3" />
                    </div>
                ) : (
                    ""
                )}

                <div className={`right ${print ? "col-12 ml-4" : "col-9"}`}>
                    {print ? (
                        <section className="personal">
                            <div className="title-wrap d-flex align-items-center">
                                <i className="fa fa-user" />
                                <span>
                                    <Translator text="personal_info" />
                                </span>
                            </div>
                            <div className="content">
                                <ContactAdress />
                                <Social />
                            </div>
                        </section>
                    ) : (
                        ""
                    )}
                    <Education />
                    <Work />
                </div>
            </div>
        </article>
    );
};

export default PageOne;
