import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import profiles from "src/static/resume.json";
import i18n from "src/i18n";
import { request } from "src/service/request";

const jsonUri = (window as any).__URL__ || null;
const lang = (i18n.language || 'vi') as string;

export const fetchApi = async () => {
    return jsonUri ? request(jsonUri) : new Promise(() => Promise.reject())
}

export interface appState {
    cv: any,
    profiles: any,
    language: string,
    print: boolean,
    page: number
}

const initialState: appState = {
    cv: (profiles as any)[lang],
    profiles: profiles,
    language: lang,
    print: false,
    page: 1
}

export const appSlice = createSlice({
    name: 'app',
    initialState,
    reducers: {
        doPrint: (state, action: PayloadAction<boolean>) => {
            return {
                ...state,
                print: action.payload,
            }
        },
        setLanguage: (state, action: PayloadAction<string>) => {
            return {
                ...state,
                language: action.payload || state.language,
                cv: state.profiles[action.payload] || state.cv
            }
        },
        setPage: (state, action: PayloadAction<number>) => {
            return {
                ...state,
                page: action.payload
            }
        },
        setApiResponse: (state, action: PayloadAction<any>) => {
            return {
                ...state,
                profiles: action.payload || state.profiles,
                cv: action.payload[state.language] || state.cv
            }
        }
    },
})

// Action creators are generated for each case reducer function
export const { doPrint, setLanguage, setApiResponse, setPage } = appSlice.actions

export default appSlice.reducer