import React, { useEffect, useMemo, useState } from "react";
import { useSelector } from "react-redux";

const Social: React.FC = () => {
    const cv = useSelector((state: any) => state.app.cv)
    const print = useSelector((state: any) => state.app.print)

    const [socials, setSocials] = useState<any[]>([]);

    useEffect(() => {
        setSocials(cv?.socials || []);
    }, [cv]);

    return useMemo(() => (
        <div>
            {print &&
                socials.map((social: any, i: number) => (
                    <div className="information" key={`social--${i}`}>
                        {social.link ? (
                            <span>
                                <i className={social.icon} />
                                &nbsp;
                                <a
                                    href={social.link}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    {social.desc}
                                </a>
                            </span>
                        ) : (
                            <span className="pl-5">{social.desc}</span>
                        )}
                    </div>
                ))}
        </div>
    ), [print, socials]);
};

export default Social;
