import React, { useEffect, useMemo, useState } from "react";
import Translator from "src/components/Translator";
import Timeline from "src/components/Timeline";
import { useSelector } from "react-redux";

const Project: React.FC = () => {
    const cv = useSelector((state: any) => state.app.cv)

    const [projects, setProjects] = useState<any[]>([]);

    useEffect(() => {
        setProjects(cv?.projects || []);
    }, [cv]);

    return useMemo(() => (
        <>
            {projects && projects.length ? (
                <section className="projects">
                    <div className="title-wrap d-flex align-items-center">
                        <i className="fa fa-file-alt" />
                        <span className="pl-5">
                            <Translator text="projects" />
                        </span>
                    </div>
                    <div className="content">
                        <Timeline items={projects} />
                    </div>
                </section>
            ) : (
                ""
            )}
        </>
    ), [projects]);
};

export default Project;
