import React, { useMemo } from "react";
import { useSelector } from "react-redux";
import Translator from "src/components/Translator";

interface PropProperties {
    descriptions: any[];
}

const DescriptionTimeline: React.FC<PropProperties> = (props: PropProperties) => {
    const { descriptions } = props;
    const print: boolean = useSelector((state: any) => state.app.print)

    return useMemo(() => (
        <>
            {descriptions && descriptions.length ?
                <ul className="project-desc">
                    {descriptions.map((desc: any, i: any) => (
                        <li key={`item--description--${i}`}>
                            <i className="fa fa-certificate" />
                            {typeof desc ===
                                "object" ? (
                                <span className="pl-5">
                                    <span className="other-text">{desc.title}&nbsp;--&nbsp;{desc.text}</span>
                                    <a className="project-code" href={desc.link} target="_blank" rel="noreferrer">
                                        <i className="fa fa-link" />&nbsp;{print ? (desc.link) : (<Translator text="source" />)}
                                    </a>
                                </span>
                            ) : (
                                <span className="pl-5">{desc}</span>
                            )}
                        </li>
                    ))}
                </ul>
                :
                <></>
            }
        </>
    ), [descriptions, print]);
};

export default DescriptionTimeline;
